.events {
  padding: 72px 52px 20px 28px;
  width: 456px;
  background-color: #fff;
  box-shadow: 0 5px 7px rgba(0, 0, 0, 0.2);
}

.headline {
  margin: 0 0 42px 0;
  padding: 0;
  font-family: "PT Serif";
  font-size: 22px;
  line-height: 24px;
  font-weight: 700;
  color: #3b3b3b;
  text-align: center;
}

.eventsList {
  margin: 0;
  padding: 0;
}

.linkShowAllEvents {
  display: block;
  margin: 0 auto 30px auto;
  width: 312px;
  height: 50px;
  background-color: #222;
  border-radius: 5px;
  text-transform: uppercase;
  font-family: "PT Sans";
  font-size: 13px;
  line-height: 50px;
  letter-spacing: 0.05em;
  font-weight: 700;
  text-align: center;
  text-decoration: none;
  color: #fff;
  cursor: pointer;
}

.linkShowAllEvents:hover {
  background-color: #fd541c;
}
