.galleryOverlay {
  background: linear-gradient(to top, #222222, #f7f7f7);
  box-shadow: 0 5px 0 rgba(0, 0, 0, 0.2);
}

.title {
  margin: 0 auto;
  padding: 76px 0 56px 0;
  width: 944px;
  font-family: "PT Serif";
  font-size: 32px;
  line-height: 24px;
  font-weight: 700;
  color: #ff6633;
}

.galleryList {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin: 0 auto;
  padding: 0 0 56px 0;
  width: 944px;
  list-style: none;
}

.galleryItem {
  margin-bottom: 20px;
  cursor: pointer;
  border: 2px solid #f7f7f7;
}

.galleryItem:hover {
  border: 2px solid #949393;
}

.image {
  margin: 0 auto;
  width: 210px;
  height: 210px;
  object-fit: cover;
}
