.listItem {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.listImg {
  padding-top: 4px;
  width: 72px;
  height: 76px;
  border-radius: 50%;
}

.listDescr {
  width: 282px;
}

.listHeadline {
  margin: 0 0 4px 0;
  padding: 0;
  font-family: "PT Serif";
  font-size: 16px;
  line-height: 24px;
  font-weight: 700;
  color: #ff6633;
}

.dateTime {
  display: flex;
  justify-content: flex-start;
}

.listDate {
  position: relative;
  margin: 0;
  padding: 0 0 8px 20px;
  font-family: "PT Sans";
  font-size: 12px;
  line-height: 20px;
  font-weight: 700;
  color: #3b3b3b;
}

.listDate::before {
  position: absolute;
  left: 0;
  top: 2px;
  content: "";
  display: inline-block;
  background-image: url(../../../images/icons/events-date.png);
  width: 14px;
  height: 14px;
}

.listTime {
  position: relative;
  margin: 0;
  padding: 0 0 0 32px;
  font-family: "PT Sans";
  font-size: 12px;
  line-height: 20px;
  font-weight: 700;
  color: #3b3b3b;
}

.listTime::before {
  position: absolute;
  left: 12px;
  top: 2px;
  content: "";
  display: inline-block;
  background-image: url(../../../images/icons/events-time.png);
  width: 14px;
  height: 14px;
}

.listText {
  margin: 0;
  padding: 0;
  font-family: "PT Sans";
  font-size: 14px;
  line-height: 20px;
  font-weight: 400;
  color: #3b3b3b;
}
