.reservationWrapper {
  padding: 72px;
  width: 456px;
  background-color: #fff;
  box-shadow: 0 5px 7px rgba(0, 0, 0, 0.2);
}

.headline {
  margin: 0 0 42px 0;
  padding: 0;
  font-family: "PT Serif";
  font-size: 22px;
  line-height: 24px;
  font-weight: 700;
  color: #3b3b3b;
  text-align: center;
}

.headlineEmpty {
  margin: 0 0 20px 0;
}

.reservationForm {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.input {
  margin-bottom: 20px;
  padding: 0 20px;
  width: 100%;
  height: 40px;
  border-radius: 4px;
  border: 1px solid #e5e4e4;
  background-color: #f7f7f7;
  background-repeat: no-repeat;
  background-position: 94% 50%;
  outline: none;
}

.inputName {
  background-image: url(../../images//icons/reservation-form-name.png);
}

.inputDate {
  background-image: url(../../images/icons/reservation-form-date.png);
}

.inputTime {
  background-image: url(../../images/icons/reservation-form-time.png);
}

.inputComments {
  background-image: url(../../images/icons/reservation-form-comments.png);
  margin-bottom: 30px;
}

.inputError {
  padding: 0;
  margin: 0 0 4px 0;
  color: #d34b4b;
  text-align: center;
}

.button {
  width: 100%;
  height: 50px;
  background-color: #222;
  border: none;
  border-radius: 5px;
  text-transform: uppercase;
  font-family: "PT Sans";
  font-size: 13px;
  line-height: 24px;
  letter-spacing: 0.05em;
  font-weight: 700;
  color: #fff;
  cursor: pointer;
  outline: none;
}

.button:hover {
  background-color: #fd541c;
}

.pickersOverlay {
  display: flex;
  justify-content: space-between;
}

.picker {
  width: 140px;
  margin-bottom: 20px;
}

.picker input {
  font-family: "PT Sans";
  font-size: 14px;
  letter-spacing: 0.05em;
  text-align: center;
}
