.eventItem {
  display: flex;
  margin-bottom: 70px;
}

.image {
  margin-right: 40px;
  width: 400px;
  height: 100%;
}

.title {
  padding: 0;
  margin: 0 0 34px 0;
  font-family: "PT Serif";
  font-size: 26px;
  line-height: 24px;
  font-weight: 700;
  color: #3b3b3b;
}

.eventItem:hover .title {
  color: #ff6633;
}

.dateTime {
  display: flex;
}

.date {
  position: relative;
  margin: 0;
  padding: 0 0 18px 20px;
  font-family: "PT Sans";
  font-size: 12px;
  line-height: 20px;
  font-weight: 700;
  color: #3b3b3b;
}

.date::before {
  position: absolute;
  left: 0;
  top: 2px;
  content: "";
  display: inline-block;
  background-image: url(../../../images/icons/events-date.png);
  width: 14px;
  height: 14px;
}

.time {
  position: relative;
  margin: 0;
  padding: 0 0 0 32px;
  font-family: "PT Sans";
  font-size: 12px;
  line-height: 20px;
  font-weight: 700;
  color: #3b3b3b;
}

.time::before {
  position: absolute;
  left: 12px;
  top: 2px;
  content: "";
  display: inline-block;
  background-image: url(../../../images/icons/events-time.png);
  width: 14px;
  height: 14px;
}

.description {
  padding: 0;
  margin: 0;
  font-family: "PT Sans";
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  color: #4b4b4b;
}

.btnReadMore {
  display: block;
  margin: 10px 0 0 auto;
  width: 160px;
  height: 50px;
  background-color: #ff6633;
  border: none;
  border-radius: 5px;
  text-transform: uppercase;
  font-family: "PT Sans";
  font-size: 13px;
  line-height: 24px;
  line-height: 50px;
  letter-spacing: 0.05em;
  font-weight: 700;
  color: #fff;
  text-decoration: none;
  text-align: center;
  cursor: pointer;
  outline: none;
}

.btnReadMore:hover,
.btnClose:hover {
  background-color: #fd541c;
}

.btnClose {
  display: block;
  margin: 10px 0 0 auto;
  width: 160px;
  height: 50px;
  background-color: #4b4b4b;
  border: none;
  border-radius: 5px;
  text-transform: uppercase;
  font-family: "PT Sans";
  font-size: 13px;
  line-height: 24px;
  line-height: 50px;
  letter-spacing: 0.05em;
  font-weight: 700;
  color: #fff;
  text-decoration: none;
  text-align: center;
  cursor: pointer;
  outline: none;
}
