.booking {
  display: flex;
  justify-content: space-between;
  padding-top: 118px;
  margin: 0 auto 116px auto;
  width: 782px;
}

.buttonBookTable {
  width: 160px;
  height: 50px;
  background-color: #ff6633;
  border: none;
  border-radius: 5px;
  text-transform: uppercase;
  font-family: "PT Sans";
  font-size: 13px;
  line-height: 24px;
  letter-spacing: 0.05em;
  font-weight: 700;
  color: #fff;
  cursor: pointer;
  outline: none;
}

.buttonBookTable:hover {
  background-color: #fd541c;
}

.bookTable {
  width: 304px;
  text-align: center;
}

.tellFriends {
  width: 304px;
  text-align: center;
}

.bookingOr {
  width: 28px;
  height: 188px;
  background-image: url(../../images/booking-or.png);
}

.headline {
  margin: 0 0 28px 0;
  padding: 0;
  font-family: "PT Serif";
  font-size: 26px;
  line-height: 24px;
  font-weight: 700;
  color: #3b3b3b;
}

.description {
  margin: 0;
  padding: 0 0 34px 0;
  font-family: "PT Sans";
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  color: #5b5b5b;
}

.tellDescription {
  margin-bottom: 16px;
}

.buttonTellFriends {
  background: none;
  border-radius: 5px;
  border: none;
  cursor: pointer;
  outline: none;
  font-size: 11px;
  line-height: 20px;
  letter-spacing: 0.05em;
  color: #fff;
}

.buttonTellFriendsFacebook {
  position: relative;
  padding-left: 20px;
  padding-right: 10px;
  margin-right: 10px;
  background-color: #3e59a5;
}

.buttonTellFriendsFacebook::before {
  position: absolute;
  left: 4px;
  top: 4px;
  content: "";
  display: inline-block;
  background-image: url(../../images/icons/booking-fb.png);
  width: 12px;
  height: 12px;
}

.buttonTellFriendsTweet {
  position: relative;
  padding-left: 20px;
  padding-right: 10px;
  background-color: #1b95e0;
}

.buttonTellFriendsTweet::before {
  position: absolute;
  left: 4px;
  top: 5px;
  content: "";
  display: inline-block;
  background-image: url(../../images/icons/booking-tw.png);
  width: 12px;
  height: 12px;
}
