.wrapper {
  margin: 0 auto;
}

.about {
  padding-top: 120px;
  padding-bottom: 120px;
  display: flex;
  justify-content: flex-end;
  background-color: #f7f7f7;
}

.description {
  box-sizing: content-box;
  padding-top: 60px;
  padding-right: 50px;
  width: 428px;
}

.headline {
  padding: 0;
  margin: 0 0 34px 0;
  font-family: "PT Serif";
  font-size: 26px;
  line-height: 24px;
  font-weight: 700;
  color: #3b3b3b;
  text-align: center;
}

.aboutText {
  padding: 0;
  margin: 0 0 12px 0;
  font-family: "PT Sans";
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  color: #4b4b4b;
}

.aboutTextDecor {
  color: #ff6633;
}

.linkAbout {
  display: block;
  margin: 34px 0 0 auto;
  width: 160px;
  height: 50px;
  background-color: #ff6633;
  border: none;
  border-radius: 5px;
  text-transform: uppercase;
  font-family: "PT Sans";
  font-size: 13px;
  line-height: 24px;
  line-height: 50px;
  letter-spacing: 0.05em;
  font-weight: 700;
  color: #fff;
  text-decoration: none;
  text-align: center;
}

.linkAbout:hover {
  background-color: #fd541c;
}

.bottomPictures {
  position: relative;
  margin-top: 30px;
  display: flex;
  justify-content: flex-end;
}

.pic1 {
  width: 636px;
  height: 498px;
  background-image: url(../../images/about-1.jpg);
}

.pic2 {
  position: absolute;
  left: -486px;
  bottom: 0;
  width: 536px;
  height: 364px;
  background-image: url(../../images/about-2.jpg);
}

.pic3 {
  margin-right: 30px;
  width: 284px;
  height: 226px;
  background-image: url(../../images/about-3.jpg);
}

.pic4 {
  width: 240px;
  height: 226px;
  background-image: url(../../images/about-4.jpg);
}
