.menu {
  padding-top: 120px;
  margin: 0 auto 120px auto;
  width: 934px;
  display: flex;
  justify-content: space-between;
}

.topPictures {
  margin-bottom: 30px;
  display: flex;
  justify-content: space-between;
}

.pic1 {
  width: 212px;
  height: 212px;
  background-image: url(../../images/menu-1.jpg);
}

.pic2 {
  width: 212px;
  height: 212px;
  background-image: url(../../images/menu-2.jpg);
}

.pic3 {
  width: 454px;
  height: 212px;
  background-image: url(../../images/menu-3.jpg);
}

.description {
  padding-top: 64px;
  width: 440px;
}

.headline {
  padding: 0;
  margin: 0 0 40px 0;
  font-family: "PT Serif";
  font-size: 26px;
  line-height: 24px;
  font-weight: 700;
  color: #3b3b3b;
  text-align: center;
}

.text {
  padding: 0;
  margin: 0 0 12px 0;
  font-family: "PT Sans";
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  color: #4b4b4b;
}

.textDecor {
  color: #ff6633;
}

.linkMenu {
  display: block;
  margin: 42px 20px 0 auto;
  width: 160px;
  height: 50px;
  background-color: #ff6633;
  border: none;
  border-radius: 5px;
  text-transform: uppercase;
  font-family: "PT Sans";
  font-size: 13px;
  line-height: 50px;
  letter-spacing: 0.05em;
  font-weight: 700;
  color: #fff;
  text-decoration: none;
  text-align: center;
}

.linkMenu:hover {
  background-color: #fd541c;
}
