.overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(179, 170, 170, 0.3);
}

.modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  justify-content: space-between;
  width: 900px;
  background-color: #fff;
  border: 10px solid #fff;
}

.modalImg {
  margin: 0 auto;
  height: 550px;
}

.btnPrev {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  border: none;
  background: none;
  cursor: pointer;
  outline: none;
}

.btnNext {
  position: absolute;
  display: flex;
  align-items: baseline;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  border: none;
  background: none;
  cursor: pointer;
  outline: none;
}

.btnPrev:hover,
.btnNext:hover {
  color: #ff6633;
}

.btnPrev[disabled],
.btnNext[disabled] {
  pointer-events: none;
  color: #c0bdbd;
}
