.footer {
  background-color: #222222;
  margin: 0 auto;
}

.footerOverlay {
  display: flex;
  justify-content: space-between;
  margin: 0 auto;
  padding: 60px 0 60px 0;
  width: 944px;
}

.logo {
  display: block;
  text-decoration: none;
  font-family: "PT Serif";
  font-size: 36px;
  line-height: 24px;
  font-weight: 700;
  color: #ff6633;
}

.footerNav {
  margin: 0 0 20px 0;
  padding: 0;
  font-family: "PT Serif";
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.075em;
  font-weight: 700;
  color: #fff;
  text-transform: uppercase;
}

.navList {
  margin: 0;
  padding: 0;
  list-style: none;
}

.navListItem {
  font-family: "PT Sans";
  font-size: 13px;
  line-height: 21px;
  letter-spacing: 0.075em;
  font-weight: 400;
  text-transform: uppercase;
}

.navListItem:not(:last-child) {
  margin-bottom: 10px;
}

.navLink {
  color: #fff;
  text-decoration: none;
}

.contact {
  width: 294px;
}

.visit,
.connect {
  margin: 0 0 16px 0;
  padding: 0;
  font-family: "PT Serif";
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.075em;
  font-weight: 700;
  color: #fff;
  text-transform: uppercase;
}

.footerDescr {
  margin: 0 0 18px 0;
  padding: 0;
  font-family: "PT Sans";
  font-size: 14px;
  line-height: 21px;
  letter-spacing: 0.075em;
  font-weight: 400;
  color: #fff;
}

.footerDescription {
  margin: 0;
}

.footerDescrDecor {
  display: inline-block;
  color: #ff6633;
}

.socialsIcon {
  width: 22px;
  height: 16px;
  border: none;
  background: none;
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
  outline: none;
}

.socialsIcon:not(:last-child) {
  margin-right: 18px;
}

.iconFacebook {
  background-image: url(../../images/icons/footer-facebook.png);
}

.iconTwitter {
  background-image: url(../../images/icons/footer-twitter.png);
}

.iconInstagram {
  background-image: url(../../images/icons/footer-instagram.png);
}

.iconTrip {
  background-image: url(../../images/icons/footer-trip.png);
}

.navLink:hover {
  color: #ff6633;
}
