.wrapper {
  margin: 0 auto;
}

.header {
  background-image: url(../../images/header.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.pageNav {
  padding-top: 42px;
  padding-bottom: 122px;
  margin: 0 auto;
  width: 942px;
  display: flex;
  justify-content: space-between;
}

.pageNavOverlay {
  width: 490px;
}

.logo {
  display: block;
  text-decoration: none;
  font-family: "PT Serif";
  font-size: 36px;
  line-height: 24px;
  font-weight: 700;
  color: #ff6633;
}

.navigation {
  display: flex;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none;
}

.navigationItem {
  width: 120px;
}

.btnRecervation {
  background: none;
  border: none;
  padding: 0;
  cursor: pointer;
  outline: none;
}

.navigationLink {
  display: block;
  text-decoration: none;
  margin-bottom: 12px;
  text-transform: uppercase;
  font-family: "PT Serif";
  font-size: 14px;
  line-height: 24px;
  font-weight: 700;
  color: #fff;
}

.navigationLink:hover {
  color: #ff6633;
}

.navigationdescr {
  padding: 0;
  margin: 0;
  font-family: "PT Sans";
  font-style: italic;
  font-size: 13px;
  line-height: 18px;
  color: #fff;
  letter-spacing: 0.075em;
}

.headline {
  padding: 0;
  margin-bottom: 6px;
  text-transform: uppercase;
  font-family: "PT Serif";
  font-size: 52px;
  line-height: 50px;
  font-weight: 700;
  color: #fff;
  text-align: center;
}

.description {
  padding: 0 0 40px 0;
  margin: 0;
  font-family: "PT Serif";
  font-size: 36px;
  line-height: 50px;
  font-weight: 400;
  color: #fff;
  text-align: center;
}

.descriptionDecor {
  text-decoration: underline;
}

.buttons {
  margin: 0 auto 144px auto;
  display: flex;
  justify-content: space-between;
  width: 342px;
}

.button {
  padding: 0;
  width: 160px;
  height: 50px;
  background-color: #000;
  border: none;
  border-radius: 5px;
  text-transform: uppercase;
  font-family: "PT Sans";
  font-size: 13px;
  line-height: 24px;
  font-weight: 700;
  letter-spacing: 0.05em;
  color: #fff;
  opacity: 0.75;
  cursor: pointer;
  outline: none;
}

.menuLink {
  line-height: 50px;
  text-align: center;
  text-decoration: none;
}

.button:hover {
  background-color: #ff6633;
  opacity: 1;
}

.scroll {
  display: block;
  padding: 0 0 10px 0;
  margin: 0 auto;
  background: none;
  border: none;
  font-family: "PT Sans";
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  text-align: center;
  color: #fff;
  cursor: pointer;
  outline: none;
}

.scroll::after {
  display: block;
  margin: 0 auto;
  padding-bottom: 42px;
  width: 14px;
  height: 14px;
  content: "";
  background-image: url(../../images/icons/scroll.png);
  background-repeat: no-repeat;
  text-align: center;
}
